import React from 'react'
import Content from '../Content'
import { kebabCase } from 'lodash'
import { Link } from 'gatsby'
import Tag from '../Tag'

const ArticleTemplate = ({
	content,
	contentComponent,
	cover,
	meta_title,
	meta_desc,
	tags,
	title,
	author,
	date,
}) => {
	const PostContent = contentComponent || Content
	return (
		<>
			<div className='py-16 text-center text-purple-100 sm_py-32 bg-gradient-to-r from-yellow-400 via-red-500 to-indigo-500'>
				<h1 className='mx-32 text-3xl font-light sm_text-5xl'>
					{title}
				</h1>
				<h2>
					{author} - {date}
				</h2>
			</div>

			<section className='container flex-grow mx-auto mt-4 sm_mt-16'>
				<div className='mx-8'>

					<div>
						<div className='flex flex-wrap justify-between my-4'>
							<Link to='/blog' className='block hover_underline'>
								‚Üê Show all
							</Link>

							{/* tags */}
							<div>
								<ul className='hidden taglist sm_block'>
									{(tags && tags.length)
										? tags.map(tag => (
											<li key={tag + `tag`} className='inline'>
												<Link to={`/tags/${kebabCase(tag)}/`}>
													<Tag key={tag} tag={tag} />
												</Link>
											</li>
										))
										: null}
								</ul>
							</div>
						</div>

						{/* <img className='my-4' src={post.frontmatter.cover} alt={post.frontmatter.title} /> */}

						<div className='max-w-4xl mx-auto my-8 markdown'>
							<PostContent content={content.html} />
						</div>

					</div>
				</div>

			</section>

		</>

	)
}

export default ArticleTemplate
